的經驗，聽得出來西打有非常多實戰經驗。"),_c('br'),_vm._v("\n        同時我也很喜歡最後 10 分鐘的總結，回顧今天的諮詢是否有結論以及接下來的進行方向。"),_c('br'),_c('br'),_vm._v("\n\n        有成果"),_c('br'),_vm._v("\n        很驚訝的是短短一小時的諮詢，就立馬制定出了接下來接案需要執行的計畫，並且西打有提供實際方向，例如作品集可以參考什麼目標進行製作。\n        ")])]),_vm._v(" "),_c('div',{staticClass:"case-student-box"},[_c('img',{directives:[{name:"lazy",rawName:"v-lazy",value:(require('@/assets/img/case/user.png')),expression:"require('@/assets/img/case/user.png')"}],staticClass:"case-student-icon",attrs:{"alt":"siddharam"}}),_vm._v(" "),_c('div',{staticClass:"case-student-title"},[_vm._v("自然教育的行政與課務專員")]),_vm._v(" "),_c('div',{staticClass:"case-student-text"},[_vm._v("\n          西打的提問精準切中，十五分鐘就解了我的關鍵問題！引導過程中思緒也很清晰落地，不過度干預而是陪伴我梳理，讓答案與選擇權交付回我的手上～"),_c('br'),_vm._v("\n          以自己的實際執行經驗分享也很專業很棒！"),_c('br'),_vm._v("\n          我由衷讚嘆「哲學諮詢」跟我擅長的聆聽方式（照顧情緒感受與歷程）的截然不同，非常俐落不沾黏也極有效率！很棒！\n        ")])]),_vm._v(" "),_c('div',{staticClass:"case-student-box"},[_c('img',{directives:[{name:"lazy",rawName:"v-lazy",value:(require('@/assets/img/case/user.png')),expression:"require('@/assets/img/case/user.png')"}],staticClass:"case-student-icon",attrs:{"alt":"siddharam"}}),_vm._v(" "),_c('div',{staticClass:"case-student-title"},[_vm._v("文書軟體國手 / 自由工作者")]),_vm._v(" "),_c('div',{staticClass:"case-student-text"},[_vm._v("\n          首先感謝 COCO 介紹西打藍這位傑出的創作者，因此毫不猶豫地選擇與西打藍進行接案諮詢。"),_c('br'),_vm._v("\n          在諮詢的過程中，西打藍很專注地挖掘與探索問題的核心，並讓我全面地理解事情的多樣性。"),_c('br'),_c('br'),_vm._v("\n          短短一小時的會議中，最後竟能復盤出了六個需要調整，並可以改善的項目，這讓我收穫豐富。"),_c('br'),_vm._v("\n          因為許多問題，都是我之前未曾考慮到的，我的觀點常受到自身狹隘視野的限制。"),_c('br'),_vm._v("\n          而在西打藍的諮詢之下，可以得到上帝視角的看法，我能夠以更全面的視角看待不同的問題。\n        ")])])])]),_vm._v(" "),_c('div',{staticClass:"case-class"},[_c('div',{staticClass:"case-class-title"},[_vm._v("是什麼樣的「藍蘋果會員」方案？")]),_vm._v(" "),_c('div',{staticClass:"case-class-outer"},[_c('div',{staticClass:"case-class-box"},[_c('img',{directives:[{name:"lazy",rawName:"v-lazy",value:(require('@/assets/img/case/one.png')),expression