內容量但不影響我的對應收入（後者有替客戶設想解決內容空缺的替代方案）最後快速獲取客戶的信任成功合作～\n        ")])]),_vm._v(" "),_c('div',{staticClass:"case-student-box"},[_c('img',{directives:[{name:"lazy",rawName:"v-lazy",value:(require('@/assets/img/case/user.png')),expression:"require('@/assets/img/case/user.png')"}],staticClass:"case-student-icon",attrs:{"alt":"siddharam"}}),_vm._v(" "),_c('div',{staticClass:"case-student-title"},[_vm._v("自由藝術工作者")]),_vm._v(" "),_c('div',{staticClass:"case-student-text"},[_vm._v("\n          ​西打有個神奇的能力跟獨有的特質，能夠同理也能夠準確的切中要害⋯⋯"),_c('br'),_vm._v("\n          ​真心傾聽、臨在的感覺讓人很自在。​不會急著要給出自己建議，也不會硬塞一些自己認為的好，只是用提問的方式，引導我自己長出力量，來找到自己內在一直都有的答案。"),_c('br'),_vm._v("\n          ​也能夠感覺到，西打的穩定，為整場對話帶來的安全感與信心。"),_c('br'),_c('br'),_vm._v("\n\n          ​整個過程幾乎是以光速在完成？但是，這麼快的情況下我竟然沒有覺得不舒服？"),_c('br'),_vm._v("\n          ​​很快的切入核心，透過引導來協助制定計劃，如何分配自己的精神、能量，去達到接案的目標跟維持品質。"),_c('br'),_c('br'),_vm._v("\n\n          ​​身為一個高敏感人，需要被細膩對待的方式，同時又處於需要釐清與歸類的腦袋打結狀態。而西打竟然完全能夠貼合自己的需要，同時具備情感上所需要的安心、信任感，又能夠理性的協助我制定計劃與如何達成目標。"),_c('br'),_c('br'),_vm._v("\n\n          ​​當有一個人真誠的傾聽你，其實你也會感覺到，那些單打獨鬥，還未能見光的部分，傷心、焦躁與壓力，有了安放的位置。​​心安了，理性上就得以接手，進入釐清的部分。\n        ")])]),_vm._v(" "),_c('div',{staticClass:"case-student-box"},[_c('img',{directives:[{name:"lazy",rawName:"v-lazy",value:(require('@/assets/img/case/user.png')),expression:"require('@/assets/img/case/user.png')"}],staticClass:"case-student-icon",attrs:{"alt":"siddharam"}}),_vm._v(" "),_c('div',{staticClass:"case-student-title"},[_vm._v("劇場工作者 / 自由工作者")]),_vm._v(" "),_c('div',{staticClass:"case-student-text"},[_vm._v("\n          可以感受到西打累積了很多諮詢的經驗，有一套提問/引導的方式，讓我在敘述問題的過程中，其實就也已經解答了自己的疑惑。"),_c('br'),_vm._v("\n          有時候會覺得自由工作者大多時候蠻孤獨的，面對每個案子、工作狀態、甚至於夥伴的各種心情只有自己知道。很感謝西打樂於分享經驗，並且把接案生涯中每個階段的重點說明得十分清楚，聽了以後覺得很有共鳴。"),_c('br'),_c('br'),_vm._v("\n          諮詢之前因工作陷入焦慮時，會把一切都想得很艱難，但彙整出行動方案之後，很驚訝發現其實這些方案都是以我現有能力就可以完成的，只缺系統化彙整和實際執行。頓時覺得有鬆了一口氣——其實已經有一副好牌在手上，接下來就是盡可能把它打得漂亮。\n\n        ")])]),_vm._v(" "),_c('div',{staticClass:"case-student-box"},[_c('img',{directives:[{name:"lazy",rawName:"v-lazy",value:(require('@/assets/img/case/user.png')),expression:"require('@/assets/img/case/user.png')"}],staticClass:"case-student-icon",attrs:{"alt":"siddharam"}}),_vm._v(" "),_c('div',{staticClass:"case-student-title"},[_vm._v("健身教練")]),_vm._v(" "),_c('div',{staticClass:"case-student-text"},[_vm._v("\n          喜歡西打用問句的方式引導我思考，不會一開始就告訴我方法，是在我思考過後再補充，也覺得分享的方法很受用，原本以為自己沒有路走了，但西打又開了很多路、很多事情要做的選項給我🤣筆記也是寫滿了兩頁，這個諮詢可以當讀書筆記在寫的這樣豐富🤣"),_c('br'),_c('br'),_vm._v("\n          後續補充：現在經營起來也越來越順了 我現在幾乎每個禮拜都有私訊詢問🥹🥹 謝謝西打給我勇氣還有方向嗚嗚"),_c('br'),_c('br'),_vm._v("\n\n          一開始我原本很聚焦於我的 IG 要不要辦新帳號，內容的部分要怎麼優化，現在路要怎麼走，但有發現西打不是聚焦這個，而是要我把手上名單整理好、照顧好舊客戶轉介紹、還有「任何人都可以合作」這個概念我很喜歡也該多加善用。"),_c('br'),_c('br'),_vm._v("\n          內容的話，感覺我把釘選、自介好好改善完，我就不會這麼執著於我的內容只能做什麼，而是可以照著4/6的比例去調整，這樣聊完讓我有方向許多！\n        ")])]),_vm._v(" "),_c('div',{staticClass:"case-student-box"},[_c('img',{directives:[{name:"lazy",rawName:"v-lazy",value:(require('@/assets/img/case/user.png')),expression:"require('@/assets/img/case/user.png')"}],staticClass:"case-student-icon",attrs:{"alt":"siddharam"}}),_vm._v(" "),_c('div',{staticClass:"case-student-title"},[_vm._v("銀行理專")]),_vm._v(" "),_c('div',{staticClass:"case-student-text"},[_vm._v("在一個工作環境久了，因為身邊都是同環境的同溫層，很容易會無法用別的角度去看待同一件事情，這時候如果能與不同領域的人來討論，就會有很多的碰撞與啟發。"),_c('br'),_vm._v("\n          而西打的引導，有專業也給人啟發，整個過程非常的舒適又有收穫，真的很棒。"),_c('br'),_vm._v("\n          短短的談話，西打卻能注意到我自己都没注意到的，我的特質與標籤，這是要累積夠多諮詢經驗才能擁有的洞察力，真的很專業\n        ")])]),_vm._v(" "),_c('div',{staticClass:"case-student-box"},[_c('img',{directives:[{name:"lazy",rawName:"v-lazy",value:(require('@/assets/img/case/user.png')),expression:"require('@/assets/img/case/user.png')"}],staticClass:"case-student-icon",attrs:{"alt":"siddharam"}}),_vm._v(" "),_c('div',{staticClass:"case-student-title"},[_vm._v("手帳創作者"),_c('br'),_vm._v("插畫及平面設計師")]),_vm._v(" "),_c('div',{staticClass:"case-student-text"},[_vm._v("\n          諮詢過程中，有分享到實際可運用的方式。比起抽象的大方向提點，這裡的建議會讓自己有頭緒 知道現在該怎麼做出下一步。"),_c('br'),_vm._v("\n          而且有針對狀況還有目